@import "../../inc/vars";
.acro-steps {
	background: #321B5F;
	color: #fff;
	text-align: center;
	&__container {
		padding: 60px 30px;
		@media(min-width: $screen-sm) {
			padding: 60px 40px;
		}
	}
	&_title {
		h2 {
			@include pop(400, 35, 1.24);
			background: var(--title-white-gradient, linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFF 0.01%, rgba(255, 255, 255, 0.60) 100%));
			background-clip: text;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: 40px;
			@media(min-width: $screen-sm) {
				margin-bottom: 31px;
				@include pop(400, 44, 1.24);
			}
		}
	}
	.acro-steps_mobile_line {
		display: block;
		margin-left: -30px;
		margin-right: -30px;
		margin-bottom: 40px;
		@media(min-width: $screen-sm) {
			display: none;
		}
		img {
			width: 100%;
		}
	}
	.steps-container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		@media(min-width: $screen-mlg) {
			background: url(/wp-content/themes/acro/images/counterpath-page/steps-line.svg) no-repeat;
			background-position: center -86%;
		}
		.step {
			width: 100%;
			display: flex;
			flex-direction: column;
			text-align: left;
			position: relative;
			margin-bottom: 24px;
			@media(max-width: $screen-sm) {
				padding-left: 64px;
				.number {
					position: absolute;
					top: 5px;
					left: 0;
					margin: 0;
				}
			}
			@media(min-width: $screen-sm) {
				width: 33.33%;
				padding: 0 30px;
				text-align: center;
			}
			@media(min-width: $screen-mlg) {
				width: 383px;
				padding: 0 90px;
			}
			.step-title {
				@include pop(400, 23, 1.36);
				margin-bottom: 6px;
			}
			.step-content {
				color: rgba(#fff, .8);
				@include fig(400, 18, 1.26);
			}
			.number {
				width: 48px;
				height: 48px;
				@include pop(400, 18, 48px);
				color: #7048C7;
				text-align: center;
				border-radius: 100%;
				background: #fff;
				@media(min-width: $screen-sm) {
					width: 72px;
					height: 72px;
					@include pop(400, 29, 72px);
					margin: 32px auto;
				}
				@media(max-width: 1400px){
					order: -1;
				}
			}
			&.step1 {
				@media(min-width: $screen-mlg) {
					padding-top: 100px;
				}
			}
			&.step2 {
				@media(min-width: $screen-mlg) {
					padding-top: 90px;
				}
			}
			&.step3 {
				@media(min-width: $screen-mlg) {
					.number {
						margin-top: 20px;
					}
				}
			}
		}
	}
}